.nav {
    width: 100%;
    height: 65px;
    position: fixed;
    line-height: 65px;
    text-align: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    z-index: 99;
}

.nav div.logo {
    /* float: left; */
    width: auto;
    height: auto;
    padding-left: 3rem;
    /* font-size: 12px; */
}

.nav div.logo a {
    text-decoration: none;
    color: #fff;
    font-size: 1.6rem;
}

.nav div.logo a:hover {
    color: #00e676;
}

.nav div.main_list {
    height: 65px;
    /* float: right; */
}

.nav div.main_list ul {
    width: 100%;
    height: 65px;
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
}

.nav div.main_list ul li {
    width: auto;
    height: 65px;
    padding: 0;
    padding-right: 3rem;
}

.nav div.main_list ul li a {
    text-decoration: none;
    color: #fff;
    line-height: 65px;
    font-size: 1.4rem;
    /* font-size: 18px; */
}

.nav div.main_list ul li a:hover {
    color: #00e676;
}

/* Home section */

.home {
    width: 100%;
    height: 100vh;
    background-image: url(/static/media/bg-temp.2d695e70.jpg);
    background-position: center top;
    background-size: cover;
}

.navTrigger {
    display: none;
}

.nav {
    padding-top: 20px;
    padding-bottom: 20px;
    transition: all 0.4s ease;
}

/* Media qurey section */

/* @media screen and (min-width: 768px) and (max-width: 1024px) {
      .container-fluid {
          margin: 0;
      }
  } */

@media only screen and (max-width: 950px) {
    .nav {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
    }

    .navTrigger {
        display: block;
        margin-right: 2rem;
    }

    .nav div.logo {
        margin-left: 15px;
    }

    .nav div.main_list {
        display: none;
    }

    .nav div.main_list_mobile {
        display: block;
        width: 0;
        height: 0;
        width: -moz-fit-content;
        width: 100vw;
        height: 100vh;
        right: 0;
        position: absolute;
        top: 64px;
        left: 0;
        bottom: 0;
        background: #111;
        transition: all 0.4s ease;
    }

    /* .nav div.show_list {
          height: auto;
          display: none;
      } */
    .nav div.main_list_mobile ul {
        flex-direction: column;
        top: 29px;
        padding-top: 1rem;
        transition: all 0.4s ease;
        margin: 0;

    }

    .nav div.main_list_mobile ul li {
        width: 100%;
        text-align: right;
        transition: all 0.4s ease;

    }

    .nav div.main_list_mobile ul li a {
        text-decoration: none;
        color: #fff;
        line-height: 65px;
        font-size: 2.4rem;
        text-align: center;
        width: 100%;
        font-size: 1.4rem;
        padding: 20px;
        transition: all 0.4s ease;

    }

    .nav div.logo a:active {
        color: #00e676;
    }

    .nav div.main_list_mobile ul li a:hover {
        color: #00e676;
    }

    .nav div.main_list_mobile ul li a:active {
        color: #00e676;
    }

    /* .nav div.media_button {
          display: block;
          border: 2px solid green;
      } */

    .nav div.main_list_mobile_hidden {
        display: none;
    }
}

/* Animation */
/* Inspiration taken from Dicson https://codemyui.com/simple-hamburger-menu-x-mark-animation/ */

.navTrigger {
    cursor: pointer;
    width: 30px;
    height: 25px;
    /* margin-top: 40px; */
    /* position: absolute; */
    /* right: 30px; */
    /* top: 0; */
    /* bottom: 0; */
}

.navTrigger i {
    background-color: #fff;
    border-radius: 2px;
    content: "";
    display: block;
    width: 100%;
    height: 4px;
}

.navTrigger i:nth-child(1) {
    -webkit-animation: outT 0.8s backwards;
    animation: outT 0.8s backwards;
    -webkit-animation-direction: reverse;
    animation-direction: reverse;
}

.navTrigger i:nth-child(2) {
    margin: 5px 0;
    -webkit-animation: outM 0.8s backwards;
    animation: outM 0.8s backwards;
    -webkit-animation-direction: reverse;
    animation-direction: reverse;
}

.navTrigger i:nth-child(3) {
    -webkit-animation: outBtm 0.8s backwards;
    animation: outBtm 0.8s backwards;
    -webkit-animation-direction: reverse;
    animation-direction: reverse;
}

.navTrigger.active i:nth-child(1) {
    -webkit-animation: inT 0.8s forwards;
    animation: inT 0.8s forwards;
}

.navTrigger.active i:nth-child(2) {
    -webkit-animation: inM 0.8s forwards;
    animation: inM 0.8s forwards;
}

.navTrigger.active i:nth-child(3) {
    -webkit-animation: inBtm 0.8s forwards;
    animation: inBtm 0.8s forwards;
}

@-webkit-keyframes inM {
    50% {
        -webkit-transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(45deg);
    }
}

@keyframes inM {
    50% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
}

@-webkit-keyframes outM {
    50% {
        -webkit-transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(45deg);
    }
}

@keyframes outM {
    50% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
    }
}

@-webkit-keyframes inT {
    0% {
        -webkit-transform: translateY(0px) rotate(0deg);
    }

    50% {
        -webkit-transform: translateY(9px) rotate(0deg);
    }

    100% {
        -webkit-transform: translateY(9px) rotate(135deg);
    }
}

@keyframes inT {
    0% {
        -webkit-transform: translateY(0px) rotate(0deg);
                transform: translateY(0px) rotate(0deg);
    }

    50% {
        -webkit-transform: translateY(9px) rotate(0deg);
                transform: translateY(9px) rotate(0deg);
    }

    100% {
        -webkit-transform: translateY(9px) rotate(135deg);
                transform: translateY(9px) rotate(135deg);
    }
}

@-webkit-keyframes outT {
    0% {
        -webkit-transform: translateY(0px) rotate(0deg);
    }

    50% {
        -webkit-transform: translateY(9px) rotate(0deg);
    }

    100% {
        -webkit-transform: translateY(9px) rotate(135deg);
    }
}

@keyframes outT {
    0% {
        -webkit-transform: translateY(0px) rotate(0deg);
                transform: translateY(0px) rotate(0deg);
    }

    50% {
        -webkit-transform: translateY(9px) rotate(0deg);
                transform: translateY(9px) rotate(0deg);
    }

    100% {
        -webkit-transform: translateY(9px) rotate(135deg);
                transform: translateY(9px) rotate(135deg);
    }
}

@-webkit-keyframes inBtm {
    0% {
        -webkit-transform: translateY(0px) rotate(0deg);
    }

    50% {
        -webkit-transform: translateY(-9px) rotate(0deg);
    }

    100% {
        -webkit-transform: translateY(-9px) rotate(135deg);
    }
}

@keyframes inBtm {
    0% {
        -webkit-transform: translateY(0px) rotate(0deg);
                transform: translateY(0px) rotate(0deg);
    }

    50% {
        -webkit-transform: translateY(-9px) rotate(0deg);
                transform: translateY(-9px) rotate(0deg);
    }

    100% {
        -webkit-transform: translateY(-9px) rotate(135deg);
                transform: translateY(-9px) rotate(135deg);
    }
}

@-webkit-keyframes outBtm {
    0% {
        -webkit-transform: translateY(0px) rotate(0deg);
    }

    50% {
        -webkit-transform: translateY(-9px) rotate(0deg);
    }

    100% {
        -webkit-transform: translateY(-9px) rotate(135deg);
    }
}

@keyframes outBtm {
    0% {
        -webkit-transform: translateY(0px) rotate(0deg);
                transform: translateY(0px) rotate(0deg);
    }

    50% {
        -webkit-transform: translateY(-9px) rotate(0deg);
                transform: translateY(-9px) rotate(0deg);
    }

    100% {
        -webkit-transform: translateY(-9px) rotate(135deg);
                transform: translateY(-9px) rotate(135deg);
    }
}

